import {
    createApp
} from 'vue'
import App from './App.vue'
import './assets/tailwind.css'


import {
    Form as FormValidate,
    Field as FieldValidate,
    ErrorMessage,
    defineRule,
    configure
} from 'vee-validate';

import {
    required,
    min,
    max,
    alpha_spaces as alphaSpaces,
    email,
    min_value as minVal,
    max_value as maxVal,
    confirmed

} from '@vee-validate/rules';


defineRule('required', required);
defineRule('min', min);
defineRule('max', max);
defineRule('alpha_spaces', alphaSpaces);
defineRule('email', email);
defineRule('min_value', minVal);
defineRule('max_value', maxVal);
defineRule('passwords_mismatch', confirmed);
defineRule('terms', required);

configure({
    generateMessage: (context) => {
        const mgs = {
            required: `The field ${context.field} is required.`,
            min: `The field ${context.field} is too short.`,
            max: `The field ${context.field} is to long.`,
            alpha_spaces: `The field ${context.field} can only contain alphabetical characters and spaces.`,
            min_value: `The field ${context.field} is too low`,
            max_value: `The field ${context.field} is too high`,
            passwords_mismatch: "Passwords don't match.",
            terms: 'You must accept the terms of service'
        }

        const msg = mgs[context.rule.name] ? mgs[context.rule.name] : `The field ${context.field} is invalid`;

        return msg;
    }
});

let app = createApp(App);

app.component('FormValidate', FormValidate);
app.component('FieldValidate', FieldValidate);
app.component('ErrorMessage', ErrorMessage);

app.mount('#app')